/* API.css */

div {
    margin: 0px;
}

h5 {
    color: #3f51b5;
    font-size: 24px;
    margin-bottom: 20px;
}



.TableContainer {
    box-shadow: 0 3px 10px rgba(0, 0, 0, 0.2);
    width: 10%; /* Decrease the width */
    margin: auto; /* Center the table */
}

.TableCell {
    font-size: 10px; /* Decrease the font size */
    padding: 4px; /* Decrease the padding */
}

.Button {
    background-color: #3f51b5;
    color: white;
    padding: 3px 6px; /* Decrease the padding */
    font-size: 10px; /* Decrease the font size */
}
